.App {
  text-align: center;
  background-color: white;
  color: rgb(4, 21, 100);
}

.App-logo {
  /*animation: App-logo-spin infinite 20s linear;*/
  height: 80px;
}

.App-header {
  background-color: white;
  height: 110px;
  padding: 20px;
  color: rgb(4, 21, 100);
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.seat {
  width:47px;
  height:50px;
  margin-left: auto;
  margin-right: auto;
  font-size: 20px;
  color: black;
  line-height: 50px; 
  display: -ms-inline-flexbox; 
  display: inline-flex;
  margin: 10px;
  padding-left: 3px;
  border-radius: 5px;
}

.neutral {
  background-color: lightgrey;
}
.cold {
  background-color: rgb(41, 130, 208);
}
.hot {
  background-color: red;
}
.coldText {
  color: rgb(41, 130, 208);
}
.hotText {
  color: red;
}

.hideMe {
  color: white;
  background-color: white;
}

.counterBlock {
  border-radius: 10px;
  display: inline;
  line-height: 80px;
  margin: 30px;
  text-align: center;
}



